@media (max-width:800px) {
    .full-box-2 {
        display: flex;
        flex-direction: column;
        place-content: center;
        place-items: center;
    }

    .paynow-button {
        transition-duration: 0s;
        place-self: center;
        align-self: center;
        justify-self: center;
    }

    .paynow-button:hover {
        background-color: black;
        color: white;
        font-size: 150%;
    }

    .paynow-button:active {
        color: rgb(255, 255, 255);
        /* font-size: 160%; */
        background-color: rgb(60, 60, 60);
    }
}